import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import { injectIntl } from 'react-intl';
import { inject } from 'mobx-react';
import { PrintPageComponent } from '@vega/print-page';

/**
* @author Saravanan kandasamy
*/

@inject('language')

class PrintContainer extends React.Component {
  static propTypes = {
    language: PropTypes.object.isRequired
  }

  render() {
    const { language } = this.props;

    return (
      <Fragment>
        <PrintPageComponent locale={language.currentLanguage} />
      </Fragment>
    );
  }
}

export default injectIntl(PrintContainer);
